
.contest-question{
    background: #fff;
    border-radius: 1.2em;
}

.question {
    color: #6041B0;
    font-size: 17px;
    font-weight: bold;
    line-height:1.4em;
}

.selected-option {
    background: #6041B0;
    border-radius: 1.5em;
    color: #F7C244;
    font-size: 17px;
    font-weight: bold;
    line-height:1.4em;
}

.settle-date{ 
    background: #F7C244;
    color: #6041B0;
    border-radius: 1.5em;
    font-size: 16px;
    font-weight: bold;
    line-height:1.4em;
}


.ui.button{
    background: #6041B0;
    color: #F7C244;
    min-height: 4em;
    font-weight: 800;
}

.my-contests .ui.header{
    color: #fff;
}

.ui.active.button {
    color: #6041B0;
    background: #F7C244;
}

.ui.active.button:hover {
    color: #6041B0;
    background: #F7C244;
}

.ui.buttons .button.active:first-child {
    border-radius: 1.5em;
}

.ui.buttons .button.active:last-child {
    border-radius: 1.5em;
}

